:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  background-color: pink;
  margin: 0;
}

.link-logo {
  vertical-align: middle;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item {
  align-items: center;
  margin-left: 20px;
  line-height: normal;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.list__item:first-child {
  margin: 0;
}

.is-active, .nav__link:hover, .nav__link.is-active {
  color: var(--main-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

